(function() {
  'use strict';

  angular
    .module('demoApp')
    .controller('IntroController', IntroController);

  /** @ngInject */
  function IntroController($timeout, demoSvc) {
    var vm = this;
    vm.lower = 10000;
    vm.upper = 50000;
    vm.time = 1000;
    vm.theValue = demoSvc.getRandomInt(vm.lower, vm.upper);
    //vm.theValue = 10;

    vm.up = function() {
      vm.theValue+=2;
    }

    var tm;
    var onTimer = function() {
      vm.theValue = demoSvc.getRandomInt(vm.lower, vm.upper);
      vm.time = demoSvc.getRandomInt(2000, 3000);
      tm = $timeout(onTimer, vm.time);
    };
    tm = $timeout(onTimer, vm.time);
  }

})();

